.preset-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.preset-dropdown {
  position: relative;
  display: inline-block;
}

.preset-options {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
  border: 1px solid #ccc;
  padding: 5px;
  margin-top: 2px;
}

.preset-dropdown:hover .preset-options {
  display: block;
}

.preset-options .btn {
  margin: 2px 0;
  width: 100%;
}

.tools-button {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  background-color: #333;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.tools-button-mobile {
  position: fixed;
  bottom: 20px;  /* Distance from the bottom */
  right: 20px;   /* Distance from the right */
  z-index: 1050; /* Ensure it's on top of other content */
}

.tools-modal {
  position: fixed;
  top: 0;
  right: 0;
  width: 80%;
  height: 100%;
  background-color: #f5f5f5;
  z-index: 1000;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #333;
  color: #fff;
  padding: 5px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .text-center {
    padding: 20px 0px !important;
    font-size: 25px;
  }

  .body.mobile-view {
    flex-direction: column;
  }
}
